<template>
  <div>
    {{ userInfo }}
    <hr />
    {{ messgeList }}
    <hr />
    {{ commentList }}
  </div>
</template>

<script>
import tools from '@/js/tools';
export default {
  data() {
    return {
      username: '',
      userInfo: {
        tbUser:{},
        tbUserInfo:{},
        userOtherInfo:{},
      },
      messgeList: [],
      messagePage: {},
      commentList: [],
      componentPage: {}
    };
  },
  created() {
    // 用户信息
    // 如果遭遇刷新则跳
    this.username = this.$route.params.username;
    tools.ajax(
      '/user/auth/getUserInfoByName',
      {
        'username': this.username
      },
      (data) => {
        this.userInfo.tbUser = data.tbUser;
        this.userInfo.tbUserInfo = data.tbUserInfo;
        this.userInfo.userOtherInfo = data.userOtherInfo;
      }
    );
    //  留言
    tools.ajax(
      '/message/queryAll',
      { info: this.username },
      (data) => {
        this.messgeList = data.list;
        this.messagePage = data.page;
      }
    );
    tools.ajax(
      '/message/queryReplyByUsername',
      { username: this.username },
      (data) => {
        this.commentList = data.list;
        this.componentPage = data.page;
      }
    );
    // 评论
  }
};
</script>
